ut.checkmobile = function(str) {
	var myreg = /^[1][3,4,5,6.7,8,9][0-9]{9}$/;
	if (!myreg.test(str)) {
		return false;
	} else {
		return true;
	}
}
//
ut.checkUsername = function(str) {
	if(/^[a-zA-Z0-9]*$/g.test(str)){
	  return true;
	}
	return false;
}
//
ut.checkName=function(name){
	const regName =/^[\u4e00-\u9fa5]{2,4}$/; 
	if(!regName.test(name)){ 
		 return false; 
	 }
	 return true;
}
//
ut.checkCard=function(id) {
	var format = /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/;
        //
        if (!format.test(id)) {
            return false;
        }
        //
        //   1900;
        var year = id.substr(6, 4),//
            month = id.substr(10, 2),//
            date = id.substr(12, 2),//
            time = Date.parse(month + '-' + date + '-' + year),//date
            now_time = Date.parse(new Date()),//
            dates = (new Date(year, month, 0)).getDate();//
        if (time > now_time || date > dates) {
            return false;
        }
        //
        var c = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];   //
        var b = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'];  //
        var id_array = id.split("");
        var sum = 0;
        for (var k = 0; k < 17; k++) {
            sum += parseInt(id_array[k]) * parseInt(c[k]);
        }
        return id_array[17].toUpperCase() === b[sum % 11].toUpperCase();
}
//
ut.checkPass=function(pass){
	const regIdNo = /(^\a-\z\A-\Z0-9\@\.)/; 
	if(!regIdNo.test(pass)){ 
		return false; 
	}
	return true
}
//
ut.loginout =function(){
	wx.setStorageSync('token','');
}
ut.request=function(parm){
	parm.data=parm.data||{};
	var c='application/x-www-form-urlencoded';
	if(parm.c){
	  c='application/json';
	}
	return new Promise(function(reject,fail){
		uni.request({
			url: parm.allurl?parm.allurl:(ut.url + parm.url),
			data: parm.data,
			method: parm.method || 'post',
			header: {
				"Accept": "*/*",
				'content-type': c,
				"Authorization": wx.getStorageSync('token')||''
			},
			success: (res) => {
				if(res.statusCode==401){
					const pages = getCurrentPages(); //
					const currentPage = pages[pages.length-1].route; //
					ut.loginout();
					if(currentPage.indexOf('pages/index')!=-1){
						wx.navigateTo({
							url: '../mine/login'
						})
					}else{
						wx.redirectTo({
							url: '../mine/login'
						})
					}
					return
				}

				if(res.data.code == 0) {
					reject(res.data.data)
				} else {
					wx.showToast({
						title: res.data.msg,
						icon: 'none',
						color: 'red',
						duration: 2000
					})
				}
			},
			fail: function() {
				fail();
				wx.showToast({
					title: '',
					icon: 'none',
					color: 'red',
					duration: 2000
				});
			}
		});
	})
};
//
ut.ajax = function(parm) {
	if(parm.loadinghide) {
		ut.request(parm);
	} else {
		wx.showLoading({
			title: '',
			complete: function() {
				ut.request(parm);
			}
		})
	}
}

ut.login = function(callback) {
	//
	wx.login({
		success: function(data) {
			console.log(data)
			wx.getUserInfo({
				success: function(res) {
					//
					ut.ajax({
						url: 'thirdParty/register',
						data: {
							js_code: data.code,
							nickName: res.userInfo.nickName,
							avatarUrl: res.userInfo.avatarUrl
						},
						method: 'get',
						success: function(data) {
							wx.setStorageSync('uid', data.id);
							wx.setStorageSync('userInfo', data.inf);
							if(callback) callback();
						}
					});
				}
			});

			//  
		}
	});
}
//
ut.uploadimg = function(parm) {
	parm=parm||{};
	wx.chooseImage({
		count:1,
		sizeType: ['compressed'], // 
		sourceType: ['album', 'camera'], // 
		success: function(res) {
			// tempFilePathimgsrc
			console.log(res)
			if(parm.callback)parm.callback(res.tempFilePaths[0]);		
		}
	})
}
//
export default ut;